@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
* {
    font-family: 'Pangolin', cursive;
}

body {
    overflow-x: hidden;
}

body,
div {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    margin-top: 10px;
}
.main_container {
    height: calc(550px * 3);
}

.section1 {
    width: 100vw;
    max-width: 100%;
    height: 550px;
    /* position: relative; */
    position: fixed;
    top: 0;
    z-index: -9;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    background-color: red;
}

.section2 {
    width: 100vw;
    max-width: 100%;
    height: 550px;
    /* position: relative; */
    position: fixed;
    top: calc(550px * 1);
    z-index: 99;

    background-color: orange;
}

.section3 {
    width: 100vw;
    max-width: 100%;
    height: 550px;
    /* position: relative; */
    position: fixed;
    top: calc(550px * 2);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    text-align: center;

    font-size: 30px;

    background-color: #676b89;
}


/* ******** Section 1 Children ********* */
.greeting {
    /* position: absolute; */
    position: fixed;
    top: 35%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 99;

    font-size: 130px;

    color: white;
}

.space {
    position: fixed;
    /* position: absolute; */
    top: 0;
}

.planet {
    width: 18%;
    height: auto;
    position: fixed;
    /* position: absolute; */
    top: 15%;
    left: 10%;
}

.earth {
    width: 40%;
    max-width: 325px;
    height: auto;
    position: fixed;
    /* position: absolute; */
    top: -10%;
    left: 80%;
}

.rocket {
    width: 25%;
    max-width: 250px;
    height: auto;
    position: fixed;
    /* position: absolute; */
    top: 40%;
    right: 10%;
}

.satellite {
    width: 10%;
    height: auto;
    position: fixed;
    /* position: absolute; */
    top: 5%;
    left: 68%;
}


/* ******** Section 2 Children ********* */
.svg_curve_top {
    width: 100%;
    height: 5vw;
    position: absolute;
    -webkit-transform: translateY(-99%);
            transform: translateY(-99%);
}

.svg_curve_bottom {
    width: 100%;
    height: 5vw;
    position: absolute;
}

.section2_container {
    padding: 30px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.left_side {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.right_side {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image {
    width: 70%;
    height: auto;
    border-radius: 50%;
    box-shadow: 0 0 10px 5px black;
}


/* ******** Section 3 Children ********* */
.input_email {
    margin: 10px 0;
    padding: 10px;
    width: 60%;
    height: 50px;
    border: 1px solid black;
    border-radius: 3px;

    font-size: 25px;

    outline: none;
}

.contacts {
    margin-top: 110px;
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: flex-start;

    color: white;
    background-color: #303443;
    box-shadow: 0 0 5px 0 #303443;
}

.contact_section {
    margin: 0 15px;
    width: 200px;
    height: 100%;

    font-size: 22px;
}

@media only screen and (max-width: 501px) {
    /* ************* Section 1 children *************** */
    .greeting {
        font-size: 60px;
    }

    .earth {
        max-width: 70%;
        top: 5%;
        left: 70%;
    }

    .rocket {
        max-width: 40%;
    }

    .satellite {
        width: 20%;
    }

    /* ************* Section 2 children *************** */
    .section2_container {
        flex-direction: column;
    }

    .left_side {
        width: 100%;
        height: 50%;
    }

    .right_side {
        width: 100%;
        height: 50%;
    }

    .image {
        width: auto;
        height: 100%;
    }

    /* ************* Section 3 children *************** */
    .contact_section {
        margin: 0 10px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        font-size: 17px;
    }
}


.button_container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.my_button {
    width: 200px;
    height: 50px;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: orange;
    cursor: pointer;
}

.button_image {
    width: 100px;
    height: 100px;
    position: absolute;
    -webkit-transform: scale(0);
            transform: scale(0);
}

.clicked {
    -webkit-animation-name: clicked;
            animation-name: clicked;
    -webkit-animation-duration: .2s;
            animation-duration: .2s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes clicked {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    10% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    20% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

@keyframes clicked {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    10% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    20% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

.launch_rocket {
    -webkit-animation-name: launch_rocket;
            animation-name: launch_rocket;
    -webkit-animation-duration: 1.7s;
            animation-duration: 1.7s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}

@-webkit-keyframes launch_rocket {
    0% {
        -webkit-transform: scale(0) translate(0%, 0%) rotate(0);
                transform: scale(0) translate(0%, 0%) rotate(0);
    }
    5% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    10% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    20% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    40% {
        -webkit-transform: scale(1) translate(0%, 0%) rotate(0);
                transform: scale(1) translate(0%, 0%) rotate(0);
    }
    100% {
        -webkit-transform: scale(1) translate(150vw, -190vh) rotate(-30deg);
                transform: scale(1) translate(150vw, -190vh) rotate(-30deg);
    }
}

@keyframes launch_rocket {
    0% {
        -webkit-transform: scale(0) translate(0%, 0%) rotate(0);
                transform: scale(0) translate(0%, 0%) rotate(0);
    }
    5% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    10% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    20% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    40% {
        -webkit-transform: scale(1) translate(0%, 0%) rotate(0);
                transform: scale(1) translate(0%, 0%) rotate(0);
    }
    100% {
        -webkit-transform: scale(1) translate(150vw, -190vh) rotate(-30deg);
                transform: scale(1) translate(150vw, -190vh) rotate(-30deg);
    }
}
